.others-list li:first-child {
  margin-bottom: 3.75rem; }

.others-list-item-brand {
  position: relative; }

.others-list span {
  font-size: 72px;
  color: #AA0601;
  font-weight: bold;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10000;
  white-space: nowrap; }

.more {
  align-items: flex-end; }

.more span::before {
  content: 'MORE';
  position: absolute;
  transition: all .65s .3s;
  transform: translateY(0px); }

.more span:hover::before {
  transform: translateY(-85px);
  opacity: .7; }

.more span::after {
  content: 'MORE';
  position: absolute;
  transition: all .65s .3s;
  transform: translateY(0px); }

.more span:hover::after {
  transform: translateY(-145px);
  opacity: .4; }

.more-img {
  position: relative;
  height: 100%;
  display: flex;
  max-width: 50%;
  transition: all .65s .3s;
  transform: translateX(0px);
  margin-right: 85px; }

.more-img::after {
  content: "";
  position: absolute;
  background: white;
  top: 8%;
  bottom: 8%;
  left: 10%;
  right: -85px; }

.more-img span {
  margin-left: 25px; }

.more-img:hover img:last-child {
  transform: translateX(15px); }

